.nav-container {
  height: 130px;
  background-color: #606065;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding: 1.2rem;
}

/* .logo-container {

} */

.logo {
  width: 80px;
  height: 80px;
  padding-top: 0px;
  margin: 0;
}

.link-container {
  margin-left: 0.4rem;
  margin-right: 4rem;
  padding: 0.8rem;
}

.link {
  text-decoration: none;
  color: #ececec;
}

.link:hover {
  color: aqua;
}

